.nav {
  &__list {
    display: flex;
    margin: 0;
    padding: 0;
  }

  &__link {
    list-style: none;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__item {
    display: block;
    line-height: 60px;
    text-decoration: none;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 500;
    text-transform: uppercase;
    color: $main-color;

    &:hover {
      color: $button-color;
    }
  }
}

.is-active {
  position: relative;
  color: $button-color;

  &::after {
    position: absolute;
    display: block;
    content: "";
    left: 0;
    bottom: 0;
    height: 4px;
    width: 100%;
    background-color: $button-color;
    border-radius: 8px;
  }
}
